import React from 'react'
import classes from './AnswerStatus.module.css'

function AnswerStatus(props) {
    let statusElement;
    if (props.status) {
        statusElement = <div className={`${classes.AnswerStatus} ${classes.CorrectStatus}`}><p>Answer is Correct</p></div>
    }
    else {
        statusElement = <div className={`${classes.AnswerStatus} ${classes.WrongStatus}`}><p>Answer is Incorrect</p></div>
    }

    return (
        <React.Fragment>
            {statusElement}
        </React.Fragment>
    )
}

export default AnswerStatus
