import React from 'react'

import classes from './Score.module.css'


function Score(props) {
    return (
        <div className={classes.Score}>
            <div className={`${classes.ScoreBar} ${classes.CorrectBar}`}>
                <p>{props.correctAnswerCount}</p>
            </div>
            <div className={`${classes.ScoreBar} ${classes.WrongBar}`}>
                <p>{props.wrongAnswerCount}</p>
            </div>
        </div>
    )
}

export default Score
