import React from 'react'
import classes from './CompletionBar.module.css'

function CompletionBar(props) {
    return (
        <div className={classes.CompletionItems}>
            <div className={`${classes.CompletionItem} ${classes.CurrentItem}`}>
                <p>{props.answeredQuestionCount}</p>
            </div>
            <div className={`${classes.CompletionItem} ${classes.TotalItem}`}>
                <p>2200</p>
            </div>
        </div>
    )
}

export default CompletionBar
