import React, { Component } from 'react'

import classes from './App.module.css';

import CompletionBar from './components/CompletionBar/CompletionBar'
import KanjiViewer from './components/KanjiViewer/KanjiViewer'
import Navbar from './components/Navbar/Navbar'
import Score from './components/Score/Score'
import Controls from './components/Controls/Controls'

import kanji_collection from './data/kanji_collection'
import AnswerStatus from './components/AnswerStatus/AnswerStatus';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      currentKanji: '',
      currentAnswer: '',
      possibleAnswers: [],
      answeredQuestionCount: 0,
      correctAnswer: 0,
      wrongAnswer: 0,
      answerStatus: null,
    }

    this.checkAnswerHandler = this.checkAnswerHandler.bind(this)
  }


  arrayShuffle(array) {
    const randomNumber = Math.floor(Math.random() * 4);

    const oldNumber = array[0];
    const temporaryNumber = array[randomNumber];

    array[randomNumber] = oldNumber;
    array[0] = temporaryNumber;
  }

  generateQuestion() {
    // Get Random Question Number
    let randomQuestionNumber = Math.floor(Math.random() * 2200);

    // Check if kanji is already Chosen
    while (kanji_collection[randomQuestionNumber].isChosen) {
      randomQuestionNumber = Math.floor(Math.random() * 2200);
    }

    // Mark as Selected
    kanji_collection[randomQuestionNumber].isChosen = true;

    let answerNumbers = [];
    answerNumbers.push(randomQuestionNumber)
    while (answerNumbers.length !== 4) {
      const randomAnswerNumber = Math.floor(Math.random() * 2200);
      if (!answerNumbers.includes(randomAnswerNumber)) {
        answerNumbers.push(randomAnswerNumber)
      }
    }

    // Shuffle Array
    this.arrayShuffle(answerNumbers)

    // Convert Number Array to Kanji Array
    answerNumbers = answerNumbers.map(answer => (kanji_collection[answer].keyword))

    this.setState({
      currentKanji: kanji_collection[randomQuestionNumber].kanji,
      currentAnswer: kanji_collection[randomQuestionNumber].keyword,
      possibleAnswers: answerNumbers
    })
  }

  checkAnswerHandler(isCorrect) {
    const increasePoint = isCorrect ? 'correctAnswer' : 'wrongAnswer'
    this.setState((prevState) => ({
      [increasePoint]: prevState[increasePoint] + 1
    }))

    this.generateQuestion();

    this.setState((prevState) => ({
      answeredQuestionCount: prevState.answeredQuestionCount + 1,
      answerStatus: isCorrect
    }))
  }

  componentDidMount() {
    this.generateQuestion();
  }


  render() {
    return (
      <div className={classes.Container}>
        <Navbar />
        <CompletionBar answeredQuestionCount={this.state.answeredQuestionCount} />
        <KanjiViewer kanji={this.state.currentKanji} />
        <Score correctAnswerCount={this.state.correctAnswer} wrongAnswerCount={this.state.wrongAnswer} />
        <Controls checkAnswerHandler={this.checkAnswerHandler} possibleAnswers={this.state.possibleAnswers} answer={this.state.currentAnswer} />
        {
          this.state.answerStatus !== null && <AnswerStatus status={this.state.answerStatus} />
        }

      </div>
    )
  }
}

export default App;